h1, .h1 {
  color: black;
  font-weight: 600;
  font-size: 1.94444rem;
  line-height: 1.3;
  margin: 0 0 20px;
}

.fs-25 {
  font-size: 1.38889rem;
}

.text-black {
  color: black;
}

*, ::after, ::before {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
  font-size: 18px;
  scroll-behavior: smooth;
  font-family: "Inter", sans-serif;
  color: #6B6B6B;
  font-weight: 500;
  background-color: #E6E6E6;
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, input, textarea, button, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, address, section, video, audio {
  border: 0;
  outline: 0;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

img {
  max-width: 100%;
}

a img {
  border: none;
}

a {
  text-decoration: none;
  transition: color .3s ease;
}

label {
  display: inline-block;
}

.align-items-center {
  align-items: center;
}

.container {
  max-width: 1400px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.container-fluid {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 3000px;
}

.d-flex {
  display: flex !important;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -20px;
  margin-right: -20px;
}

.row > * {
  flex-shrink: 0;
  width: 100%;
  max-width: 100%;
  padding-right: 20px;
  padding-left: 20px;
}

.justify-content-between {
  justify-content: space-between;
}

.justify-content-end {
  justify-content: flex-end;
}

.nowrap {
  white-space: nowrap;
}

.text-center {
  text-align: center !important;
}

.text-end {
  text-align: right !important;
}

@media (min-width: 576px) {
  .col-sm-auto {
    flex: 0 0 auto;
    width: auto;
  }
  .col-sm-6 {
    flex: 0 0 auto;
    width: 50%;
  }
}

@media (min-width: 992px) {
  .col-lg-6 {
    flex: 0 0 auto;
