  .mb-sm_20 {
    margin-bottom: 20px !important;
  }
}

@media (min-width: 1200px) {
  .pe-xl_40 {
    padding-right: 40px !important;
  }
}

.form-area {
  border-radius: 15px;
  background-color: #E6E6E6;
  padding: 46px 20px 0;
}

.form-control {
  display: block;
  width: 100%;
  padding: .53rem 15px .55rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #6B6B6B;
  background-color: #EFEFEF;
  background-clip: padding-box;
  border: 1px solid #CBCBCB;
  appearance: none;
  border-radius: 5px;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.form-control::placeholder {
  opacity: 1;
}

.form-control.form-control-small {
  background-color: white;
  width: 50px;
  text-align: center;
  padding: .11rem 5px .14rem;
}

.form-text {
  text-align: right;
  font-size: 0.88889rem;
  margin-top: 3px;
}

.form-check {
  min-height: 1.5rem;
  padding-left: 17px;
  display: flex;
  align-items: center;
}

input.form-check-input {
  border-radius: 5px;
  float: left;
  margin: 0 7px 0 -17px;
  width: 26px;
  height: 25px;
  border: 1px solid #CBCBCB;
  background-color: #EFEFEF;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-print-color-adjust: exact;
  color-adjust: exact;
  print-color-adjust: exact;
}

input.form-check-input:checked {
  background-color: #73D412;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3e%3cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='m6 10 3 3 6-6'/%3e%3c/svg%3e");
  background-size: 80% auto;
  background-repeat: no-repeat;
  background-position: center;
  border-color: #73D412;
}

.form-check-label {
  font-size: 0.88889rem;
}

.form-check-2 input.form-check-input, .form-check-3 input.form-check-input {
  background-color: white;
}

.form-check-2 input.form-check-input:checked, .form-check-3 input.form-check-input:checked {
  background-color: #73D412;
}

@media (min-width: 575px) {
  .form-area {
    padding: 46px 33px;
  }
  .form-control {
    padding: .33rem 15px .36rem;
  }
  .form-text {
    font-size: 0.77778rem;
    color: #969696;
  }
  .form-text a {
    text-decoration: underline;
    color: #969696;
  }
  .form-text a:hover, .form-text a:active {
    text-decoration: none;
  }
  .form-check-label {
    font-size: 0.77778rem;
  }
  input.form-check-input {
    width: 22px;
    height: 21px;
    margin: 1px 5px 0 -17px;
  }
  .form-check-2 .form-check-label {
    font-size: 1.38889rem;
  }
  .form-check-2 input.form-check-input, .form-check-3 input.form-check-input {
    margin-right: 10px;
  }
}

.btn, a.btn, .header-main a.btn {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 1.38889rem;
  line-height: 1.1;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  -webkit-appearance: button;
  padding: 16px 73px 19px;
  border-radius: 37px;
  border: 1px solid;
}

.btn-primary, a.btn-primary, .header-main a.btn-primary {
  background-color: black;
  border-color: black;
  color: white;
}

.btn-primary:hover, .btn-primary:active, a.btn-primary:hover, a.btn-primary:active, .header-main a.btn-primary:hover, .header-main a.btn-primary:active {
  background-color: #4c4c4c;
}

.btn-grey, a.btn-grey, .header-main a.btn-grey {
  color: #1D1D1F;
  background-color: #D2D2D2;
  border-color: #D2D2D2;
}

.btn-grey:hover, .btn-grey:active, a.btn-grey:hover, a.btn-grey:active, .header-main a.btn-grey:hover, .header-main a.btn-grey:active {
  background-color: #f0f0f0;
}

.btn-medium, a.btn-medium, .header-main a.btn-medium {
  font-size: 0.83333rem;
  padding: 9px 42px;
}

@media (min-width: 575px) {
  .btn {
    font-size: 1.11111rem;
    padding: 13px 73px 14px;
  }
}

.custom-tooltip {
  background-color: black;
  color: white;
  display: inline-block;
  border-radius: 10px;
  padding: 5px 10px;
  font-size: 0.66667rem;
  position: relative;
}

.custom-tooltip::after {
  content: '';
  position: absolute;
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: black transparent transparent transparent;
  bottom: -4px;
  left: 50%;
  margin-left: -2px;
}

.header-main {
  background-color: #1D1D1F;
  padding: 11px 0;
}

.header-main a {
  color: white;
  font-size: 0.83333rem;
  margin-left: 45px;
}

.header-main-logo a {
  display: inline-block;
  margin: 0;
}

.header-main-logo img {
