  }
  .header-main-logo {
    margin: 10px 0 20px;
  }
  .header-right-menu {
    margin-bottom: 10px;
  }
}

.footer-main {
  background-color: white;
  padding: 18px 0 19px;
  text-align: center;
}

.footer-main ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer-main ul li {
  margin: 10px 20px;
}

.footer-main ul li a {
  font-weight: 600;
  color: #949191;
  font-size: 0.83333rem;
}

.footer-main ul li a:hover, .footer-main ul li a:active {
